body {
  &::-webkit-scrollbar {
    width: 12px;
  }

  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 4px rgba($blue, 0.14);
    background: $grey;
  }

  &::-webkit-scrollbar-thumb {
    background: $blue;
    border-radius: 10px;
    box-shadow: inset 0 0 4px rgba($blue, 0.16);
  }
}
